import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Button } from "react-bootstrap";
import * as styles from "./buttons.module.css";

type SummerSchoolButtonProps = {
  href: string;
};

const SummerSchoolButton: React.FC<SummerSchoolButtonProps> = ({ href }) => {
  const { t } = useTranslation();
  return (
    <Button
      className={styles.button}
      target={"_blank"}
      rel={"noreferrer"}
      href={href}
    >
      {t("register")}
    </Button>
  );
};

export default SummerSchoolButton;
