import React from "react";
import { graphql, PageProps } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";

import Layout from "../layout/layout";
import Seo from "../layout/seo/seo";
import SummerSchool from "../components/summer_school/pages/summer_school";
import { currentEdition } from "../data";

const WorkshopsPage: React.FC<PageProps> = ({ location }) => {
  const { t } = useTranslation();

  return (
    <Layout>
      <Seo title={t("title")} pathname={location.pathname} />

      <SummerSchool edition={currentEdition} />
    </Layout>
  );
};

export default WorkshopsPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: {
        ns: { in: ["common", "summer_school"] }
        language: { eq: $language }
      }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
