import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Edition } from "../../../data";
import Balancer from "react-wrap-balancer";
import { getDate } from "../../../hooks/useEditionData";
import SummerSchoolButton from "../../elements/buttons/summerSchoolButton";
import * as styles from "../../hackathon/pages/hackathon.module.css";

type TrackProps = {
  title: string;
  date: string;
  description: string;
  expect: string;
  program: string[];
  href: string;
};

const Track: React.FC<TrackProps> = ({
  title,
  date,
  description,
  expect,
  program,
  href,
}) => (
  <div>
    <div style={{ padding: "5vh 0" }}>
      <h2 className={"title"}>{title}</h2>
      <h4 className={"date"}>{date}</h4>
    </div>
    <p className={"p"}>{description}</p>
    <p className={"p"}>{expect}</p>
    <ul>
      {program.map((item, index) => (
        <li key={"SummerSchoolTrack" + index} className={"p"}>
          {item}
        </li>
      ))}
    </ul>
    <div style={{ display: "flex", justifyContent: "center" }}>
      <SummerSchoolButton href={href} />
    </div>
  </div>
);

type SummerSchoolProps = {
  edition: Edition;
};

const SummerSchool: React.FC<SummerSchoolProps> = ({ edition }) => {
  const { t } = useTranslation();
  const program = t("general.program", { returnObjects: true }) as string[];

  return (
    <div className={"page"} id={"summer_school"}>
      <h1 className={"title"}>
        <Balancer>{t("title")}</Balancer>
      </h1>
      <h3 className={"date"}>
        <Balancer>{t("location")}</Balancer>
      </h3>

      <div className={styles.info}>
        <div style={{ paddingBottom: "3vh" }}>
          <p className={"p"}>{t("general.description")}</p>
          {program.map((item, index) => (
            <li key={"SummerSchool" + index} className={"p"}>
              {item}
            </li>
          ))}
        </div>
        <Track
          title={t("beginner.title")}
          date={getDate(edition, "summerSchoolBeginner")}
          description={t("beginner.description")}
          expect={t("expect")}
          program={t("beginner.program", { returnObjects: true })}
          href={"https://forms.gle/CEvcum4eQSg4qDbh8"}
        />
        <Track
          title={t("advanced.title")}
          date={getDate(edition, "summerSchoolAdvanced")}
          description={t("advanced.description")}
          expect={t("expect")}
          program={t("advanced.program", { returnObjects: true })}
          href={"https://forms.gle/cBWKxCAJRAkvXtAE8"}
        />
        <p className={"p"} style={{ paddingTop: "5vh", textAlign: "center" }}>
          {t("confusion")}
        </p>
      </div>
    </div>
  );
};

export default SummerSchool;
